.header{
    position:fixed;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: transparent;
    z-index: 1;
  }
  
  /*NAVBAR*/
  /*NAVBAR*/
  /*NAVBAR*/
  /*NAVBAR*/
  /*NAVBAR*/
  
  .navbar{
    display: flex;
    justify-content: center;
    width: 42vw;
    margin: .75% 0 .75% 0;
    padding: .8% 0 .8% 0;
    border-radius: 20px;
    background-color: transparent;
  }
  
  .navbar-links{
    margin:0 1% 0 1%;
    font-weight: 700;
    transition: .5s;
    padding: 1% 3.5% 1.5% 3.5%;
    color: var(--text-color);
    border-radius: 20px;
    text-shadow: 0 0 1.5rem var(--main-color);
    font-size: 1.5rem;
  }
  
  #menu-icon{
    position: fixed;
    font-size: 5rem;
    color: var(--text-color);
    display: none;
    right: 3vw;
    top: 3vh;
  }
  
  /*animating the navbar elements*/
  .navbar-links:nth-of-type(1){
    animation: fadeInRight 1.5s ease forwards;
  }
  .navbar-links:nth-of-type(2){
    animation: fadeInRight 1.5s ease forwards;
  }
  .navbar-links:nth-of-type(3){
    animation: zoomIn 1.5s ease forwards;
  }
  .navbar-links:nth-of-type(4){
    animation: fadeInLeft 1.5s ease forwards;
  }
  .navbar-links:nth-of-type(5){
    animation: fadeInLeft 1.5s ease forwards;
  }
  /*end*/
  
  .navbar-links:hover, .navbar-links.active{
    color: var(--main-color);
    text-shadow: 0 0 3rem var(--main-color);
  }

  
